* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Comic Sans MS, cursive, sans-serif;
}

body {
  color: #fff;
  text-align: center;
  background: linear-gradient(45deg, red, #f90, #ff0, #3c3, #39f, #90c);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  animation: 10s linear infinite rainbow-bg;
  display: flex;
}

@keyframes rainbow-bg {
  0% {
    filter: hue-rotate();
  }

  100% {
    filter: hue-rotate(360deg);
  }
}

button {
  color: #000;
  cursor: pointer;
  text-transform: uppercase;
  background: radial-gradient(circle, #fc0, #f30);
  border: 3px solid #fff;
  border-radius: 50px;
  padding: 15px 30px;
  font-size: 20px;
  font-weight: bold;
  box-shadow: 0 4px 10px #00000080;
}

button:hover {
  background: radial-gradient(circle, #f60, #c00);
  transform: scale(1.1)rotate(5deg);
}

input {
  text-align: center;
  background-color: #fff;
  border: 3px solid #fc0;
  border-radius: 20px;
  outline: none;
  padding: 12px;
  font-size: 18px;
  font-weight: bold;
}

.timer-set {
  background: #fffc;
  border-radius: 30px;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding: 20px;
  display: flex;
  box-shadow: 0 10px 30px #00000080;
}

.timer {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.value {
  color: #fc0;
  text-shadow: 2px 2px 5px #000c;
  font-size: 50px;
  font-weight: bold;
}

.arrow, .tusks {
  color: #fc0;
  font-size: 24px;
  text-decoration: none;
  transition: color .3s, transform .3s;
}

.arrow:hover, .tusks:hover {
  color: #f30;
  transform: scale(1.2);
}

.list-of-tusk {
  background: #fffc;
  border-radius: 30px;
  justify-content: center;
  gap: 25px;
  padding: 20px;
  list-style: none;
  display: flex;
  box-shadow: 0 10px 30px #00000080;
}
